#navbar {
  display: flex;
  flex-direction: column;

  height: 100%;
  width: 300px;
  background-color: $color-purple;
  position: fixed;
  z-index: 100;
}

.oslo__logo {
  cursor: pointer;
  margin-top: 3rem;
  margin-bottom: 2rem;
  align-self: center;
}

.navigation-link {
  $p: &;

  position: relative;
  background-color: $color-purple;
  color: rgba(white, 0.9);
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;

  cursor: pointer;
  width: 100%;

  &--active {
    background-color: $color-blue;
    transition: all 0.3s ease-in-out;

    #{$p}__label {
      color: $color-purple;
      font-weight: 500;
    }

    label:hover {
      background: none !important;
    }

    input[type='checkbox']:checked + label {
      &::after {
        transform: scale(0);
      }
    }
  }

  &--compare {
    background-color: lighten($color-purple, 5%);
    transition: background-color 0.3s ease-in-out;
  }

  &__label {
    letter-spacing: 0.3px;
    display: flex;
    flex-grow: 1;
    height: 40px;
    align-items: center;
    position: relative;

    &-compare {
      margin-bottom: 2rem;
      margin-top: 2rem;
      padding-left: 3.6rem;
    }

    // Add visual border on the left side of text on hover
    &::before {
      content: '';
      display: none;
      position: absolute;
      width: 10px;
      top: 0;
      left: -10px;
      bottom: 0;
      background: $color-purple;
      pointer-events: none;
    }
  }

  &:not(&--active) &__label:hover {
    background-color: darken($color-purple, 5%);

    // Show the visual border on hover
    &::before {
      background-color: darken($color-purple, 5%);
      display: block;
    }
  }
}

input[type='checkbox'] {
  display: none;

  & + label {
    cursor: pointer;
    display: inline-block;
    height: 40px;
    position: relative;
    vertical-align: middle;
    width: 50px;
    padding-left: 1rem;

    &:hover {
      background: darken($color-purple, 10%);

      &::before {
        opacity: 0.8;
        background: rgba(white, 0.15);
      }
    }

    // checked inside border
    &::after {
      background-color: $color-blue;
      border-radius: 1px;
      content: '';
      height: 10px;
      left: 24px;
      position: absolute;
      top: 14px;
      width: 10px;
      transform: scale(0);
      transition: all 0.3s ease-in-out;
    }

    // unchecked border
    &::before {
      -moz-border-radius: 1px;
      -webkit-border-radius: 1px;
      border: 1px solid white;
      border-radius: 1px;
      content: '';
      height: 18px;
      left: 20px;
      opacity: 0.35;
      position: absolute;
      top: 10px;
      width: 18px;
      transition: all 0.3s ease-in-out;
    }
  }

  &:checked + label {
    position: relative;
    opacity: 1;

    // checked inside border
    &::after {
      transform: scale(1);
      transition: transform 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }

    // checked border
    &::before {
      border: 1px solid $color-blue;
      opacity: 1;
      transition: all 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }
  }
}

.navigation-drawer__buttons {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  padding-right: 1rem;

  .navigation-drawer__button-container {
    align-self: flex-end;

    .navigation-drawer__button {
      border: 1px solid rgb(248, 198, 107);
      color: rgb(248, 198, 107);
      font-weight: bold;
      padding: 0.3rem 1rem 0.3rem 1rem;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      border-radius: 5px;
      margin: 0.5rem;
      transition: all 0.3s ease-in-out;

      &:disabled {
        opacity: 0.2;
        transition: opacity 0.3s ease-in-out;
      }

      &:hover:not([disabled]) {
        box-shadow: 0 0 0 1px #e0e0e0;
      }
    }
  }
}

.navigation-drawer__select-container {
  display: flex;
  justify-content: flex-end;
  position: relative;

  .navigation-drawer__select {
    border: 1px solid rgb(248, 198, 107);
    width: 205px;
    margin: 0.5rem;
    color: rgb(248, 198, 107);
    font-weight: bold;
    padding: 0.5rem 1rem 0.5rem 1rem;
    position: relative;
  }

  &:before {
    content: '';
    border-left: 2px solid rgb(248, 198, 107);
    border-bottom: 2px solid rgb(248, 198, 107);
    width: 0.5rem;
    height: 0.5rem;
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    transform: rotate(-45deg);
  }
}
