

































































@import './../styles/colors';

.oslo__card-image {
  background-blend-mode: multiply;
  color: white;
  cursor: pointer;
  height: 160px;
  width: 280px;
  padding: 1em;
  display: flex;
  flex-direction: column;
}

.oslo__category {
  align-self: flex-start;
  padding: 0.25rem 0.5rem;
  color: $color-purple;
  font-weight: bold;
  margin-bottom: 0.5em;
  align-items: flex-start;
  text-transform: uppercase;
}

.oslo__tema {
  font-size: 1.5em;
  font-weight: bold;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.78);
  line-height: 1.15em;
}

.oslo__see-more-text {
  margin-top: auto;
  font-size: 1.15rem;
  font-weight: 500;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.78);
}
