






































































































































@import '../styles/colors';

.oslo__navigation-topbar {
  background-color: white;
  border-bottom: 1px solid $color-grey-300;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  width: 100%;

  &-button {
    display: flex;
    flex-direction: row;
  }
}

.oslo__topbar {
  color: $color-purple;
  font-weight: bold;
  margin-bottom: 2rem;

  &-text {
    letter-spacing: 0.8px;
    margin-left: 1rem;
    margin-top: 0.3rem;
    text-transform: uppercase;
  }
}

.label {
  font-size: 16px;
  transition: all 0.3s ease-in-out;

  &--active {
    left: 0;
    right: auto;
    position: absolute;
    font-size: 16px;
    transform: translateY(-18px) translateX(-12px) scale(0.75);
    transition: all 0.3s ease-in-out;
  }
}

.navigation-topbar {
  &__select {
    position: relative;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 0.7px;
    text-transform: uppercase;
    display: flex;
    justify-content: space-between;
    color: rgb(141, 141, 160);

    &:before {
      bottom: -1px;
      content: '';
      left: 0;
      position: absolute;
      transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
      width: 100%;
      border: 0.5px solid black;
    }

    &:focus {
      outline: none;
    }

    &:focus:after {
      transform: scaleX(1);
    }

    &:after {
      bottom: -1px;
      content: '';
      position: absolute;
      transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
      width: 100%;
      border: 1px solid $color-purple;
      transform: scaleX(0);
    }
  }

  &__dropdown {
    position: absolute;
    border: 1px solid black;
    display: flex;
    flex-flow: row wrap;
    z-index: 1;
    background-color: white;

    &-column {
      display: flex;
      flex-direction: column;
      padding: 1rem;

      &--heading {
        margin-bottom: 1rem;
        border-top: 3px solid rgb(182, 63, 50);
        width: 190px;
        font-weight: bold;
      }
    }

    &-items {
      display: flex;
      flex-direction: column;
    }

    &-item {
      background-color: rgb(245, 245, 245);
      height: 34px;
      padding: 0.5rem;
      margin-bottom: 0.1rem;
      color: rgb(41, 40, 88);
      border-radius: 4px;

      &--active {
        background-color: rgb(110, 233, 255);
      }
    }
  }
}
